	        if (self.iface) {
	            with_checkpoint(
	                self.model,
	                modify,
	                cockpit.format(_("Deleting <b>$0</b> will break the connection to the server, and will make the administration UI unavailable."),
	                               self.dev_name),
	                cockpit.format(_("Delete $0"), self.dev_name),
	                true);
	        }
